import React from 'react'
import classes from './AddDialog.module.css'
import DialogItem from './DialogItem/DialogItemTS'
import MessageItem from './MessageItem/MessageItemTS'
import {
    Formik,
    Form,
    Field
} from 'formik'

export type dialogItemType = {
    userPhoto: string | undefined
    name: string
    key: number
    id: number
}

export type messageItemType = {
    message: string
    id: number
}

type dialogType = {
    state: any
    userPhoto: string
    userName: string
    postNewText: (postText: string) => void
}

const AddDialog: React.FC<dialogType> = (props) => {

    type initialValuesType = {
        textMessage: string
    }

    const initialValues: initialValuesType = {
        textMessage: ''
    }

    const onSubmit = (values: initialValuesType, onSubmitProps: any) => {
        if (values.textMessage === '') return
        else {
            postNewText(values.textMessage)
            onSubmitProps.resetForm()
        }
    }

    const postNewText = (postText: string) => {
        props.postNewText(postText)
    }

    let dialogElements: Array<dialogItemType> = props.state.dialogsData.map((dialog: dialogItemType) =>
        <DialogItem
            userPhoto={props.userPhoto}
            name={props.userName}
            key={dialog.id}
            id={dialog.id} />
    )

    let messageItems = props.state.messagesData.map((message: messageItemType) =>
        <MessageItem
            message={message.message}
            id={message.id} />
    )

    return (
        <div className={classes.dialogsContainer}>

            <div className={classes.dialogs}>
                {dialogElements}
            </div>
            <div className={classes.messages}>
                {messageItems}
            </div>

            <div className={classes.addDialog}>
                <hr />
                <p></p>
                <div>
                    <Formik initialValues={initialValues} onSubmit={onSubmit}>
                        <Form>
                            <h2>
                                <label htmlFor="textMessage">Send Message</label>
                            </h2>
                            <div></div>
                            <Field as='textarea' name='textMessage' id='textMessage' />
                            <div></div>
                            <button type='submit'>Send</button>
                        </Form>
                    </Formik>
                </div>
            </div>
        </div>
    )
}
export default AddDialog