import React from 'react'
import { postNewTextActionCreator } from '../../redux/dialogsReducerTypescript'
import AddDialog from './AddDialog/AddDialogTS'
import {connect} from 'react-redux'
import { LoggedInRedirect } from '../../hoc/LoggedInRedirect';
import { compose } from 'redux';
import { AppStateType } from '../../redux/reduxStoreTS';

type dialogPropsType = {
    state: any
    userPhoto: string
    userName: string
    postNewTextActionCreator: (postText: string) => void
}

class AddDialogContainerTS extends React.Component<dialogPropsType> {

    render() {
        return <AddDialog 
        state={this.props.state}
        userPhoto={this.props.userPhoto}
        userName={this.props.userName}
        postNewText={this.props.postNewTextActionCreator}
        />
    }

}

let mapStateToProps = (state : AppStateType) => {

    return {
        state: state.dialogsReducer,
        userPhoto: state.headerReducer.userProfilePhoto,
        userName: state.authReducer.login
    }
    
}

export default compose<React.ComponentType>(
    connect(mapStateToProps, {postNewTextActionCreator}),
    LoggedInRedirect
)(AddDialogContainerTS)





